<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page,index) of pages" :key="index">
        <div class="icon" v-for="item of page" :key="item.index">
          <div class="icon-img">
            <img class="icon-img-content" :src="item.imgUrl" />
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination'
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
  .icons >>> .swiper-wrapper
    height:0
    padding-bottom:50%
  .icon
    position:relative
    width:25%
    height:0
    float:left
    padding-bottom:20%
    overflow:hidden
    .icon-img
      position:absolute
      top:15%
      left:0
      right:0
      bottom:.44rem
      .icon-img-content
        height:100%
        display:block
        margin:0 auto
    .icon-desc
      position:absolute
      left:0
      right:0
      bottom:0
      height:.44rem
      line-height:.44rem
      color:#333
      text-align:center
      overflow:hidden
      white-space:nowrap
      text-overflow:ellipsis
</style>



